.App {
  text-align: center;
  background-image: url(./images/maleroom-background.png);

  background-repeat: no-repeat;
  background-size: cover;
 
 
  

}

.h1{
  font-size: 150px; 
}
.App-header {
  margin: 30px;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  
}

