a{
  padding-left: 50px;
    padding-right: 50px;
  padding-top: 50px;
}

.card{
  font-weight: bolder;
  padding: 20px;
 
  opacity: .8;
  color: black;
  font-family: 'Fredericka the Great', cursive;
  box-shadow: 0px 0px 10px black;
}
#acEvents{
  height: auto;
  width:30%;
}
#Navbar{
  box-shadow: 0px 3px 10px black;
}
#ticket{
  width: 120px;
  height: auto;
  min-width: 20%;

}
#Player{

  padding: 10px;
box-shadow: 0px 0px 10px 5px;
}
#MapContainer{
  height: 20px;
  width: 20px;
}
.Info{
  margin-top: 40px;
}